var _self;
export default {
	data() {
		return {
			rfidList:''
		}
	},
	onLoad(e) {
		_self = this;
		this.getServerData(e);
	},
	onReady() {
		
	},
	methods: {
		getServerData(e) {
			let rfid=e.rfid;
			console.log(rfid);
			uni.showLoading({
				title: "正在加载数据..."
			});
			let headers = {};
			headers['content-type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
			uni.request({
				url: this.$url + '/php/detail.php',//此处使用了全局变量拼接url（main.js文件中），关于全局变量官方问答里有
				method: 'POST',//get或post
				header: headers,
				data: {
					rfid:rfid
				},
				success: function(res) {
					let data = res.data;
					if (data.ec === 0) {
						_self.rfidList=data.user;
					} else {
						uni.showToast({
							title: data.msg
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: '网络错误'
					});
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
	}
}
